          },
          content: `<p>蒲公英屬（學名：Taraxacum）植物通稱蒲公英、黃花地丁、婆婆丁，華花郎、蒲公草、食用蒲公英、尿床草、西洋蒲公英，為菊目菊科的一屬，是溫帶至亞熱帶常見的一種植物。蒲公英採孤雌生殖，葉邊的形狀像獅子的尖牙。其英文名字Dandelion來自法語dent-de-lion，正是獅子牙齒之意，蒲公英花語為我在遠處為你的幸福而祈禱。</p>
<p>蒲公英為多年生草本植物，花莖是空心的，折斷之後有白色的乳汁。匙形或狹長倒卵形的葉子呈蓮座狀平鋪，羽狀淺裂或齒裂。</p>
