          <p>2020 年零售業者因新冠肺炎疫情影響紛紛開展線上通路，仍有不少店家因為商品性質而觀望不前。以婚宴會場、精品活動合作聞名的花藝店「花意空間」，卻超前部署拓展電商通路，透過客製化的花束搶佔線上商機、更在今年母親節檔期利用線上網店提升品牌曝光，大幅增加品牌的會員與 LINE 好友數，以利進行顧客再行銷。透過電商通路作為銷售生力軍，結合實體店面提供的專業花藝服務，在疫情間仍創下好的銷售成績，正是花意空間把握數位轉型的美好成果。</p>
          `,
