  methods: {
    makeToast(color, content) {
      const nowTime = new Date();
      const timeConvert = (time) => {
        const h = time.getHours();
        const m = time.getMinutes();
        const s = time.getSeconds();
        const hh = h > 9 ? h : `0${h}`;
        const mm = m > 9 ? m : `0${m}`;
        const ss = s > 9 ? s : `0${s}`;
        return `${hh}:${mm}:${ss}`;
      };
      this.$bvToast.toast(content, {
        variant: color,
        title: `系統訊息 ${timeConvert(nowTime)}`,
        autoHideDelay: 5000,
      });
    },
    go_page(route) {
      this.$router.push(route);
    },
    /* eslint-disable */
    fb_login() {
      const resposeFunc = (res) => {
        if (res.status === 'connected') {
          // fb授權成功
          FB.api('/me?fields=name,id,email', (response) => {
            const memberId = `fb__${response.id}`;
            const ifHasMember = this.members.findIndex((item) => item.id === memberId);
            if (ifHasMember < 0) {
              // 首次使用fb登入，註冊並登入
              const newMember = {
                id: memberId,
                name: `${response.name}`,
                gender: '',
                birthday: '',
                tel: '',
                email: `${response.email}`,
                password: '',
                favorites: [],
              };
              this.register(newMember);
              this.login(memberId);
            } else {
              // 非首次使用fb登入，直接登入
              this.login(memberId);
            }
          });
        } else {
          // fb授權失敗
          this.$bus.$emit('show_toast', 'warning', 'FB 授權登入失敗');
        }
      };
      const fbScope = {
        scope: 'email, public_profile',
        return_scopes: true,
      };
      FB.login(
          (response) => {
            resposeFunc(response);
          },
          fbScope
      );
    },
    /* eslint-enable */
    login(id) {
      const thisMember = this.members.find((item) => item.id === id);
      this.member = thisMember;
      this.$bus.$emit('show_toast', 'success', '登入成功');
    },
    logout() {
      this.member = {
        id: '',
        name: '',
        gender: '',
        birthday: '',
        tel: '',
        email: '',
        password: '',
        favorites: [],
      };
      this.$bus.$emit('show_toast', 'success', '登出成功');
      this.go_page('');
    },
    register(newMember) {
      this.members.push(newMember);
      this.$bus.$emit('show_toast', 'success', '註冊成功');
    },
  },
  created() {
    this.$bus.$on('son_call_data', () => {
      this.$bus.$emit('son_get_data', this.$data);
    });
    this.$bus.$on('show_toast', (color, content) => {
      this.makeToast(color, content);
    });

    this.$bus.$on('add_new_mail', (mail) => {
      this.mails.push(mail);
    });
    this.$bus.$on('toggle_mail_read', (id, boolean) => {
      const oriMailIndex = this.mails
        .findIndex((item) => item.id === id);
      this.mails[oriMailIndex].read = boolean;
    });
    this.$bus.$on('submit_mailReply', (id, replyObj) => {
      const tMailIndex = this.mails
        .findIndex((item) => item.id === id);
      this.mails[tMailIndex].reply = replyObj;
      this.$bus.$emit('show_toast', 'success', '回覆信件成功');
    });

    this.$bus.$on('add_new_member', (member) => {
      this.register(member);
    });
    this.$bus.$on('delete_member', (id) => {
      const oriMemberIndex = this.members.findIndex((item) => item.id === id);
      this.members.splice(oriMemberIndex, 1);
      this.$bus.$emit('show_toast', 'success', '刪除會員成功');
    });

    this.$bus.$on('log_in', (member) => {
      this.login(member.id);
    });
    this.$bus.$on('log_out', () => {
      this.logout();
    });
    this.$bus.$on('fb_login', (boolean) => {
      if (boolean) {
        this.fb_login();
        // profile: {email: '', id: '', name: ''},
      }
    });

    this.$bus.$on('edit_favorite', (id, boolean) => {
      if (boolean) {
        this.member.favorites.push(id);
      } else {
        const idIndex = this.member.favorites.findIndex((item) => item === id);
        this.member.favorites.splice(idIndex, 1);
      }
    });

    this.$bus.$on('add_new_order', (newOrder) => {
      this.orders.push(newOrder);
    });
    this.$bus.$on('make_payment', (id, payment) => {
      const paidOrder = this.orders.find((item) => item.id === id);
      paidOrder.payment.info = payment;
      paidOrder.payment.ifPaid = true;
      this.$bus.$emit('show_toast', 'success', `訂單${id}付款完成`);
    });
    this.$bus.$on('delete_order', (orderId) => {
      const thisOrderIndex = this.orders.findIndex((item) => item.id === orderId);
      this.orders.splice(thisOrderIndex, 1);
      this.$bus.$emit('show_toast', 'success', `訂單${orderId}已刪除`);
    });

    this.$bus.$on('admin_login', (admin) => {
      this.administrator = admin;
    });
    this.$bus.$on('admin_logout', () => {
      this.administrator = {
        id: '',
        name: '',
        account: '',
        password: '',
      };
    });

    this.$bus.$on('add_new_product', (product) => {
      const repeatP = this.products.find((item) => item.id === product.id);
      if (repeatP !== undefined) {
        this.$bus.$emit('show_toast', 'warning', '產品ID重複，新增失敗');
      } else {
        const newP = JSON.parse(JSON.stringify(product));
        this.products.push(newP);
        this.$bus.$emit('show_toast', 'success', '新增商品成功');
      }
    });
    this.$bus.$on('edit_product', (product) => {
      const newP = JSON.parse(JSON.stringify(product));
      const newId = newP.id;
      const oldIndex = this.products.findIndex((item) => item.id === newId);
      this.products[oldIndex] = newP;
      this.$bus.$emit('show_toast', 'success', '變更商品成功');
    });
    this.$bus.$on('delete_product', (product) => {
      const theId = product.id;
      const theIndex = this.products.findIndex((item) => item.id === theId);
      this.products.splice(theIndex, 1);
    });

    this.$bus.$on('add_new_news', (nNews) => {
      this.news.push(nNews);
      this.$bus.$emit('show_toast', 'success', '新增新聞成功');
    });
    this.$bus.$on('edit_news', (newsObj) => {
      const thisId = newsObj.id;
      const oriNewsObjIndex = this.news.findIndex((item) => item.id === thisId);
      this.news[oriNewsObjIndex] = newsObj;
      this.$bus.$emit('show_toast', 'success', '編輯新聞內容成功');
    });
    this.$bus.$on('delete_news', (newsId) => {
      const oriNewsObjIndex = this.news.findIndex((item) => item.id === newsId);
      this.news.splice(oriNewsObjIndex, 1);
      this.$bus.$emit('show_toast', 'success', '刪除新聞成功');
    });

    this.$bus.$on('add_new_ois', (newOis) => {
      this.orderInfos.push(newOis);
      this.$bus.$emit('show_toast', 'success', '購物指南新增成功');
    });
    this.$bus.$on('edit_ois', (oisObj) => {
      const oriOisIndex = this.orderInfos.findIndex((item) => item.id === oisObj.id);
      this.orderInfos[oriOisIndex] = oisObj;
      this.$bus.$emit('show_toast', 'success', '購物指南編輯成功');
    });
    this.$bus.$on('delete_ois', (oisId) => {
      const oriOisIndex = this.orderInfos.findIndex((item) => item.id === oisId);
      this.orderInfos.splice(oriOisIndex, 1);
      this.$bus.$emit('show_toast', 'success', '購物指南刪除成功');
    });

    this.$bus.$on('add_new_coupon', (couponObj) => {
      this.coupons.push(couponObj);
      this.$bus.$emit('show_toast', 'success', '優惠券新增成功');
    });
    this.$bus.$on('edit_coupon', (couponObj, oriIndex) => {
      this.coupons[oriIndex] = couponObj;
      this.$bus.$emit('show_toast', 'success', '優惠券編輯成功');
    });
    this.$bus.$on('delete_coupon', (couponIndex) => {
      this.coupons.splice(couponIndex, 1);
      this.$bus.$emit('show_toast', 'success', '優惠券刪除成功');
    });

    this.$bus.$on('add_new_admin', (adminObj) => {
      this.administrators.push(adminObj);
      this.$bus.$emit('show_toast', 'success', '管理員新增成功');
    });
    this.$bus.$on('edit_admin', (adminIndex, adminObj) => {
      this.administrators[adminIndex] = adminObj;
      this.$bus.$emit('show_toast', 'success', '管理員資料更新成功');
    });
    this.$bus.$on('delete_admin', (id) => {
      const targetIndex = this.administrators.findIndex((item) => item.id === id);
      this.administrators.splice(targetIndex, 1);
      this.$bus.$emit('show_toast', 'success', '管理員刪除成功');
    });

    this.$bus.$on('send_order', (orderList) => {
      orderList.forEach((orderId) => {
        const thisOrderIndex = this.orders.findIndex((item) => item.id === orderId);
        this.orders[thisOrderIndex].sending.ifSent = true;
        const newSendingInfo = {
          sentTime: (new Date()).getTime(),
        };
        this.orders[thisOrderIndex].sending.info = newSendingInfo;
        this.$bus.$emit('show_toast', 'success', `訂單${orderId}出貨成功`);
      });
    });
  },
  mounted() {
    /* eslint-disable */
    window.fbAsyncInit = function () {
      FB.init({
      appId: "256052322609908",
      cookie: true,
      xfbml: true,
      version: "v9.0",
      });
      FB.AppEvents.logPageView();
    };
    /* eslint-enable */
    document.title = '艸芊芳苑 | FLORAL DESIGN';
  },
};
</script>
